* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: black;
  user-select: none;
}
h5 {
  margin-block-start: 0;
  margin-block-end: 0;
}
.MAIN {
  width: 95%;
  max-width: 380px;
  margin: 5vh auto;
}

.MAIN section {
  display: grid;
}

.MAIN section .pra-contact {
  display: flex;
  justify-content: space-evenly;
  padding: 10px;
  align-items: center;
  border-radius: 16px;
  -webkit-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  -moz-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
}

.pra-section {
  display: flex;
  flex-direction: column;
}

.pra-img {
  display: flex;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  user-select: none;
  -webkit-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  -moz-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
}

.pra-contact .pra-img img {
  border-radius: 50%;
  z-index: 1;
}
.pra-img::before {
  content: "";
  width: 100px;
  height: 100px;
  position: absolute;
  border-radius: 50%;
  background-color: transparent;
  z-index: 10;
}

.pra-name {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
}

.pra-det {
  display: flex;
  width: 50%;
  flex-direction: column;
  justify-content: space-around;
}

.pra-contact .pra-det .cont {
  display: flex;
  width: 100%;
  border-radius: 16px;
  padding: 2px 5px;
  align-items: center;
  align-content: center;
  margin-bottom: 10px;
  -webkit-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  -moz-box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
  box-shadow: 0px 0px 7px 0px rgba(173, 173, 173, 0.466);
}

.pra-icons {
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  margin-right: 10px;
}
.pra-icons:hover {
  -webkit-box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
  -moz-box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
  box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
}
.pra-contact .pra-det .cont:nth-child(1) span:nth-child(1) {
  color: #2196f3;
}

.pra-contact .pra-det .cont:nth-child(2) span:nth-child(1) {
  color: #f44336;
}

.pra-sel-us {
  user-select: text;
}

.pra-contact .pra-det .cont:nth-child(2) span h5 {
  width: 100px;
  word-break: break-all;
}

.pra-contact div {
  display: flex;
  user-select: none;
}

.pr-scroll-x {
  width: 100%;
  overflow-x: scroll;
  align-items: flex-start !important;
}

.pr-skill-container {
  display: flex;
}

.pra-skill {
  display: flex;
  width: 100px;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin-right: 20px;
  padding: 2px;
  border-radius: 10px;
}

.pra-skill div {
  display: flex;
  justify-content: space-between;
}

.pra-skill img {
  width: 100%;
}

.pr-flex-direction-col {
  flex-direction: column !important;
}
.pr-flex-direction-row {
  flex-direction: row !important;
}

.pr-aline-start {
  align-items: flex-start !important;
}

.pr-aline-center {
  align-items: center !important;
}

.pra-box-shadow {
  -webkit-box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
  -moz-box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
  box-shadow: 0px 0px 7px 5px rgba(173, 173, 173, 0.466);
}

.pr-li- {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: black;
  margin-right: 5px;
}

.pr-scroll {
  max-width: 380px;
  width: 100%;
  padding: 10px;
}

.pr-remove-padd {
  /* padding: 10px !important; */
}

.spacer {
  margin: 7px 0px;
}

svg {
  margin: auto;
}

#mask {
  animation: mask 975ms ease infinite;
}
@keyframes mask {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(280px);
  }
}
